body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, #ffffff, #ffffff, #ffffff, #548c9a);
}

.main-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-style: solid;
  border-width: 1px;
  border-color: #508C9B;
  margin: 20px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  max-width: 100%;
  margin: 50px;
  width: 400px;
  padding: 5px;
  border-radius: 8px;
  border-width: 1px;
  border-style: solid;
  border-color: #201E43;
  background-color: #f9f9f9;
  box-shadow: 0 2px 10px rgba(32, 30, 67, 0.1);
}

option{
  background-color: #ffffff;
}

h2 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
  color: #201E43;
}

.form-group {
  display: flex;
  flex-direction: column;
  width: 120%; 
  margin-bottom: 15px;
  padding-right: 50px;
}

.form-group label {
  margin-bottom: 5px;
  font-weight: 600;
  color: #508C9B;
  text-align: left; 
  width: 100%;
}

.form-control {
  width: 100%;
  margin: 0 auto; 
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  box-sizing: border-box;
}

.btn-primary {
  width: 70%;
  padding: 10px 0;
  background-color: #134B70;
  border: none;
  border-radius: 4px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.btn-primary:hover {
  background-color: #508C9B;
}

.error-message {
  margin-top: 15px;
  color: red;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
}

@media (max-width: 768px) {
  .form-group {
    width: 100%; 
  }
}

@media (max-width: 480px) {
  .auth-form {
    padding: 10px;
  }

  h2 {
    font-size: 18px;
  }

  .btn-primary {
    font-size: 14px;
    padding: 8px 0;
  }
}
.switch{
  background: transparent;
  border: 0;
  color:#134B70;
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;

}
